import {Pipe, PipeTransform} from '@angular/core';
import {Status} from "../../_core/enumerates";

@Pipe({
  name: 'status'
})

export class StatusPipe implements PipeTransform {
  transform(value: number): string {
    switch (value) {
      case Status.ACTIVE:
        return 'Active';
      case Status.DISABLED:
        return 'Disabled';
    }
  }
}
