import {NgModule} from '@angular/core';
import {HTTP_INTERCEPTORS} from "@angular/common/http";
import {RestBaseInterceptor} from "./rest-base.interceptor";
import {LoadingIndicatorInterceptor} from "./loading-indicator/loading-indicator.interceptor";
import {ErrorInterceptor} from "./error.innterceptor";
import {NprogressHandlerService} from "./loading-indicator/nprogress-handler.service";
import {MockModule} from "./mock/mock.module";
import {DataSupplierService} from "./smart-resolver/data-supplier.service";

@NgModule({
 imports: [
   MockModule
 ],
 providers: [
   {
     provide: HTTP_INTERCEPTORS,
     useClass: RestBaseInterceptor,
     multi: true,
   },
   {
     provide: HTTP_INTERCEPTORS,
     useClass: LoadingIndicatorInterceptor,
     multi: true,
   },
   {
     provide: HTTP_INTERCEPTORS,
     useClass: ErrorInterceptor,
     multi: true,
   },
/*   {
     provide: HTTP_INTERCEPTORS,
     useClass: MockInterceptor,
     multi: true,
   },*/

   NprogressHandlerService,
   DataSupplierService
 ]
})
export class RestApiModule { }

