import { NgModule } from '@angular/core';
import {MockInterceptor} from "./mock.interceptor";
import {HTTP_INTERCEPTORS} from "@angular/common/http";


@NgModule({
/*
 imports: [],
 exports: [],
*/
 providers: [
   {
     provide: HTTP_INTERCEPTORS,
     useClass: MockInterceptor,
     multi: true,
   }
 ]
})
export class MockModule { }
