
/*
* Usage:

  1)this.sortHelper = new SortHelper(this.transactions, 'id');

  2)
  <thead (click)="onHeaderClick($event)">
  onHeaderClick(e) {
   const clmName = e.target.getAttribute('sortable-column');
    this.sortHelper.onSortByColumn(clmName);
  }

  3) getTransactions() {
    return this.sortHelper.sort();
  }

* */

export class SortHelper {
  private isAscending = true;

  constructor(private dataArr: any[], private clmName: string) { }

  // TH click processor
  onSortByColumn(clmName) {

    if (this.clmName == clmName) {
      this.isAscending = !this.isAscending;
    }
    else {
      this.clmName = clmName;
      this.isAscending = true;
    }
  }

  sort(dataArr = this.dataArr) {
    return dataArr.sort(this.sortFun);
  }


  private sortFun = (a: any, b: any) => {
    let value;
    const clmName = this.clmName;
    if (typeof a[clmName] === 'number') {
      value = a[clmName] - b[clmName];
    }
    else if (typeof a[clmName] === 'string') {
      value = (a[clmName] > b[clmName]) ? 1 : -1;
    }
    else if (a[clmName] instanceof Date) {
      value = a[clmName].getTime() - b[clmName].getTime();
    }

    return ((this.isAscending ? 1 : -1) * value);
  };


}