import {Component, OnInit} from '@angular/core';
import {OrdersProgressService} from "./orders-progress.service";
import {DataSupplierService} from "../../_core/rest-api/smart-resolver/data-supplier.service";
import {ExchangesOrdersService} from "./exchanges-orders.service";
import {ExecutionBreakdownsService} from "./execution-breakdowns.service";

@Component({
    selector: 'xtd-orders-routing-charts',
    template: `
      <div class="row">
        <div class="col-md-4">
          <xtd-chart [chartOptions]="ordersProgressBarOptions"></xtd-chart>
        </div>
        <div class="col-md-4">
          <xtd-chart [chartOptions]="exchangesOrdersPieOptions"></xtd-chart>
        </div>

        <div class="col-md-4">
          <xtd-chart [chartOptions]="executionBreakdownsPieOptions"></xtd-chart>
        </div>
      </div>
    `,
    providers: [
      OrdersProgressService,
      ExchangesOrdersService,
      ExecutionBreakdownsService
    ]
  }
)

export class ChartsContainerComponent implements OnInit {

  ordersProgressBarOptions;
  exchangesOrdersPieOptions;
  executionBreakdownsPieOptions;

  constructor(private ds: DataSupplierService,
              private ordersProgressServ: OrdersProgressService,
              private exchangesOrdersServ: ExchangesOrdersService,
              private executionBreakdownsServ: ExecutionBreakdownsService) { }

  ngOnInit() {
    const {ordersProgress, ordersSpread, executionBreakdowns} = this.ds.getData();

    this.ordersProgressBarOptions = this.ordersProgressServ.composeChartOptions(ordersProgress);
    this.exchangesOrdersPieOptions = this.exchangesOrdersServ.composeChartOptions(ordersSpread);
    this.executionBreakdownsPieOptions = this.executionBreakdownsServ.composeChartOptions(executionBreakdowns);

  }
}