
import {throwError as observableThrowError, Observable, of} from 'rxjs';
import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Router} from "@angular/router";
import {LOGIN, LOGOUT} from "../_core/rest-api/rest-api";
import {showError} from "../_shared/alert";
import {LoginPair} from "./login-pair";
import {AuthTokenService} from "../_core/auth/auth-token.service";
import {switchMap} from "rxjs/internal/operators";


export const LOGIN_FORM_PAGE = '/login';
const LOGIN_PAIR_KEY = 'login_pair';

@Injectable()
export class LoginService {

  private returnUrl = '';

  constructor(private http: HttpClient,
              private router: Router,
              private authTokenService: AuthTokenService) {
  }

  // XHR ---
  login(loginPair?: LoginPair): Observable<boolean> {
    const isPairFromStorage = !loginPair;
    loginPair = loginPair || this.getLoginPair();
    // no login pair either in storage or as parameter
    if (!loginPair) {
      return observableThrowError('LoginService: no login pair');
    }
    return this.http.post(LOGIN, loginPair)
      .pipe(
        switchMap((res) => {
            if (res['error']) {
              if (isPairFromStorage) { // wrong login in storage (exceptional case)
                this.removeLoginPair();
              }
              return observableThrowError(res['error']['message']);
            }
            const token = res['result']['token'];
            this.authTokenService.saveToken(token);
            return of(true);
          }
        )
        // do((token: string) => this.authTokenService.saveToken(token))
      )

  }

  logout() {
    return this.http.get(LOGOUT).subscribe(
      () => {
        this.authTokenService.removeToken();
        this.removeLoginPair();
        this.router.navigate([LOGIN_FORM_PAGE]);
      },
      (error) => {
        showError('Failed to logout.');
      }
    );
  }

// storage ---
  saveLoginPair(loginPair) {
    localStorage.setItem(LOGIN_PAIR_KEY, JSON.stringify(loginPair));
  }

  getLoginPair() {
    const ls = localStorage.getItem(LOGIN_PAIR_KEY);
    if (!ls) {
      return null;
    }
    return JSON.parse(ls);
  }

  removeLoginPair() {
    localStorage.removeItem(LOGIN_PAIR_KEY);
  }

// returnUrl - navugate to this URL after login
  getReturnUrl(): string {
    return this.returnUrl;
  }

  setReturnUrl(url: string): void {
    this.returnUrl = url;
  }

  resetReturnUrl(): void {
    this.returnUrl = '';
  }

}
