import {RouterModule, Routes} from '@angular/router';
import {ModuleWithProviders} from '@angular/core';
import {MainLayoutComponent} from "../my-smartadmin/shared/layout/app-layouts/main-layout.component";
import {MarketDataComponent} from "./market-data/market-data.component";
import {NewsFeedComponent} from "./news-feed/news-feed";
import {CoLocationComponent} from "./co-location/co-location.component";
import {UserComponent} from "./user/user.component";
import {SmartResolver} from "./_core/rest-api/smart-resolver/smart-resolver";
import * as API from "./_core/rest-api/rest-api";
import {LoginComponent} from "./login/login.component";
import {AuthLayoutComponent} from "../my-smartadmin/shared/layout/app-layouts/auth-layout.component";
import {AuthGuard} from "./_core/auth/auth.guard";
import {TransactionsComponent} from "./transactions/transactions.component";
import {OrdersRoutingComponent} from "./orders-routing/orders-routing.component";
import {XtrdDashboardComponent} from "./Dashboard/xtrd_dashboard.component";

export const MARKET_DATA_ROUTE = 'market-data';
export const NEWS_FEED_ROUTE = 'news-feed';
export const CO_LOCATION_ROUTE = 'co-location';
export const ORDERS_ROUTING_ROUTE = 'orders-routing';



// Note:MainLayoutComponent and AuthLayoutComponent are SmartAdmin's components!
export const routes: Routes = [
  {
    path: '',
    component: MainLayoutComponent,
    // TODO: uncomment it!
    // canActivateChild: [AuthGuard],

    children: [
      {
        path: '', redirectTo: 'dashboard', pathMatch: 'full'
      },

      {
        path: 'dashboard',
        component: XtrdDashboardComponent,
        resolve: { resolvedData: SmartResolver },
        data: {
          toResolve: {
            xtrdServices: API.GET_XTRD_SERVICES,
            transactions: API.GET_TRANSACTIONS,
            ordersSpread: API.GET_ORDERS_SPREAD,
            executionBreakdowns: API.GET_EXECUTION_BREAKDOWNS,
            ordersProgress: API.GET_ORDERS_SENDING_PROGRESS
          }
        }
      },
      {
        path: MARKET_DATA_ROUTE,
        component: MarketDataComponent,
        resolve: {resolvedData: SmartResolver},
        data: {
          toResolve: {
            exchanges: API.GET_EXCHANGES,
            appliedExchangesIds: API.GET_SELECTED_EXCHANGES
          }
        }
      },
      {
        path: NEWS_FEED_ROUTE,
        component: NewsFeedComponent
      },
      {
        path: CO_LOCATION_ROUTE,
        component: CoLocationComponent
      },
      {
        path: ORDERS_ROUTING_ROUTE,
        component: OrdersRoutingComponent,
        resolve: {resolvedData: SmartResolver},
        data: {
          toResolve: {
            exchanges: API.GET_EXCHANGES,
            ordersRoutings: API.GET_ORDERS_ROUTINGS,
            ordersTimeline: API.GET_ORDERS_TIMELINE
          }
        }
      },

      {
        path: 'transactions',
        component: TransactionsComponent,
        resolve: {resolvedData: SmartResolver},
        data: {
          toResolve: {
            transactions: API.GET_TRANSACTIONS,
          }
        }
      },

      {
        path: 'user',
        component: UserComponent,
      }
    ]
  },

  {
    path: 'login',
    component: AuthLayoutComponent,
    children: [
      {
        path: '',
        component: LoginComponent
      }
    ]

  },

//  {path: '**', redirectTo: 'miscellaneous/error404'}

];


export const routing: ModuleWithProviders = RouterModule.forRoot(routes, {useHash: true});
