import {Injectable} from "@angular/core";

@Injectable()
export class FormValidationHelperService {
  checkOnSubmit(form: any): boolean {
    const elems = form.elements;
    let isValid = true;
    for (let e of elems) {
      const cl = e.classList;
      if (cl.contains('ng-invalid')) {
        isValid = false;
      }
      this.showErrorState(cl);
    }
    return isValid;
  }

  invalidateField(inputField) {
    const cl = inputField.classList;
    cl.add('ng-invalid');
    this.showErrorState(cl);
  }

  private showErrorState(classList): void {
    classList.remove('ng-untouched');
    classList.remove('ng-pristine');
    classList.add('ng-touched');
    classList.add('ng-dirty');
  }
}