import {Injectable} from '@angular/core';
import {BehaviorSubject} from "rxjs";
import {Post} from "../models/post_DELETE";

// TODO: use enums of the real application!
export const enum BCC { // broad cast chanel
 // POST,
 // COMMENT
}

@Injectable()
export class BroadcastService {

  private readonly chanels:  Array<BehaviorSubject<any>>;

  constructor() {
    this.chanels = [];
  //  this.chanels[BCC.POST] = new BehaviorSubject<Post>(null);
  //  this.chanels[BCC.COMMENT] = new BehaviorSubject<Comment>(null);
  }

  getChanel(broadcastChanel: number): BehaviorSubject<any> {
    return this.chanels[broadcastChanel];
  }

  emit(broadcastChanel: BCC, value: Object): void {
    this.chanels[broadcastChanel].next(value);
  }

}
