import {Component, OnInit} from '@angular/core';
import {AddNewRoutingService} from "./add-new-routing.service";
import {BsModalRef} from "ngx-bootstrap";
import {OrdersRouting} from "../_core/models/ordersRouting";
import {showError} from "../_shared/alert";
import {FormValidationHelperService} from "../_core/form-validation-helper/form-validation-helper.service";

@Component({
  selector: 'xtd-add-new-routing-form',
  templateUrl: 'add-new-routing.component.html',
  providers: [AddNewRoutingService]
})

export class AddNewRoutingComponent implements OnInit {

  // input parameter
  exchanges: any[];
  // callback
  addRouterDialogCallback: Function;

  newRouting: OrdersRouting;
  selectedExchangeName;

  constructor(public modalRef: BsModalRef,
              private serv: AddNewRoutingService,
              private formVldr: FormValidationHelperService) { }

  ngOnInit() {
    this.newRouting = new OrdersRouting(<OrdersRouting>{});
    this.selectedExchangeName = null;
  }

  setExchangeId(selectedExchange) {
    this.newRouting.exchange_id = selectedExchange.item.id;
  }

  saveNewOrderRouting(e) {
    
    // 1. check required fileds
    let isOk = this.formVldr.checkOnSubmit(e.target);
    // 2. check if exchange selected from a list
    if (!this.newRouting.exchange_id) {
      this.formVldr.invalidateField(e.target.exchange);
      isOk = false;
    }

    if (!isOk) {
      return false;
    }

    this.serv.saveNewRouting(this.newRouting)
      .subscribe(
        () => { 
          this.addRouterDialogCallback(this.newRouting);
          this.modalRef.hide();
        },
        (err) => showError('Failed to save new order routing.')
      );
  }
}