import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Params, Resolve} from '@angular/router';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {forkJoin, Observable, of, Subject} from "rxjs";
import {catchError, map} from "rxjs/operators";
import {concatMap, delay, switchMap, tap} from "rxjs/internal/operators";
import {DataSupplierService} from "./data-supplier.service";
import {TO_USE_REST_MOCKUP} from "../../../app.config";

/*
* Usage:
*
*   1. in the router:
*
*       resolve: {resolvedData: SmartResolver},
*       data: {
*         toResolve: {
*           post: GET_POST,
*           comments: GET_COMMENTS
*         }
*       }
*
*   2. in a component
*
*   const resolvedData = route.snapshot.data['resolvedData'];
*    this.post = resolvedData['post'];
*    this.comments = resolvedData['comments'];
*
*    OR use
*
*    this.getData(route);
*
* */


@Injectable()
export class SmartResolver implements Resolve<Observable<any>> {

  private toResolveUrls: string[]; // contains parameters, like ":id"

  constructor(private http: HttpClient, private dataSupplier: DataSupplierService) {
  }

  resolve(route: ActivatedRouteSnapshot): Observable<any> {
    this.toResolveUrls = route.data['toResolve'];
    // const params = route.params;
    // const reqs: Observable<Object>[] = [];

    const urls = this.getUrls(route.params);

    const reqFunc: Function = TO_USE_REST_MOCKUP ? this.mockReqs : this.joinReqs;
    ;

    return reqFunc.call(this, urls)
      .pipe(
        map(data => this.arrayToHash(data)),
        tap(data => this.dataSupplier.setData(data))
      )

      /*    for (const uKey in this.toResolveUrls) {
            let reqUrl = this.toResolveUrls[uKey];

            if (typeof reqUrl == 'function') {
              // reqs.push(reqUrl());
              throw new Error('SmartResolver: Not implemented using function instead of URL!')
            }

            for (const pKey in params) {
              reqUrl = reqUrl.replace(':' + pKey, params[pKey]);
            }

            if (reqUrl.indexOf(':') !== -1) {
              throw new Error('SmartResolver: URL does not contain required parameter!');
            }

            reqs.push(this.http.get(reqUrl)
              .pipe(
                catchError((error: HttpErrorResponse) => {
                  // skip 404 error which is leagal response of a server!
                  return of(null);
                })
              )
            );
          }*/

      /*    return forkJoin(...reqs)
            .pipe(
              map(data => this.arrayToHash(data)),
              tap(data => this.dataSupplier.setData(data))
            )*/
      ;
  }

  private getUrls(params: Params): string[] {
    const reqUrlsArr = [];
    for (const uKey in this.toResolveUrls) {
      let reqUrl = this.toResolveUrls[uKey];

      if (typeof reqUrl == 'function') {
        throw new Error('SmartResolver: Not implemented using function instead of URL!')
      }

      for (const pKey in params) {
        reqUrl = reqUrl.replace(':' + pKey, params[pKey]);
      }

      if (reqUrl.indexOf(':') !== -1) {
        throw new Error('SmartResolver: URL does not contain required parameter!');
      }

      reqUrlsArr.push(reqUrl)
    }

    return reqUrlsArr;
  }


  private joinReqs(urls) {
    let reqs: Observable<Object>[];
    reqs = urls.map((u) => {
        return this.http.get(u)
          .pipe(
            catchError((error: HttpErrorResponse) => {
              // skip 404 error which is leagal response of a server!
              return of(null);
            })
          )
      }
    );
//     reqs.push(this.http.get(urls)
//       .pipe(
//         catchError((error: HttpErrorResponse) => {
//           // skip 404 error which is leagal response of a server!
//           return of(null);
//         })
//       )
//     );

    return forkJoin(...reqs);
    /*      .pipe(
            map(data => this.arrayToHash(data)),
            tap(data => this.dataSupplier.setData(data))
          )*/
  }


  private mockReqs(urls) {
    let reqs: Observable<Object>[];
    reqs = urls.map((u, idx) => {
        return of(u)
          .pipe(
            delay(500 * idx),
            switchMap(u => this.http.get(u))
          )
      }
    );

    return forkJoin(...reqs);
  }

  private arrayToHash(data) {
    let i = 0;
    const o = {};
    for (const uKey in this.toResolveUrls) {
      o[uKey] = data[i];
      i++;
    }
    return o;
  };

}
