import {Component} from '@angular/core';
import {LoginService} from "./login.service";
import {AuthTokenService} from "../_core/auth/auth-token.service";
import {showWarn} from "../_shared/alert";
import {LoginPair} from "./login-pair";
import {Router} from "@angular/router";
import {FormValidationHelperService} from "../_core/form-validation-helper/form-validation-helper.service";

@Component({
  templateUrl: 'login.component.html',
})
export class LoginComponent {
  loginPair: LoginPair = {login: '', password: ''};
  rememberMe = true;

  constructor(
    private router: Router,
    private loginService: LoginService,
    private authTokenService: AuthTokenService,
    private vldr: FormValidationHelperService) {}

  doLogin(e) {

    if (!this.vldr.checkOnSubmit(e.target)) {
      return false;
    }

    this.loginService.login(this.loginPair).subscribe(
      () => this.onSuccessfulLogin(),
      (err: string) => showWarn(err)
    )
  }

  private onSuccessfulLogin() {
    if (this.rememberMe) {
      this.loginService.saveLoginPair(this.loginPair);
    }

    const url = this.loginService.getReturnUrl();
    this.loginService.resetReturnUrl();
    this.router.navigate([url]);

  }

}