// Note: The following code used directly without dependency injection!

const ERROR_TIMEOUT = 4000;
const WARN_TIMEOUT = 3000;
const INFO_TIMEOUT = 2000;

/************** public, global(!) functions ************/
export const showError = (text: string, title: string = "") => Alert.error(text, title);
export const showWarn = (text: string, title: string = "") => Alert.warn(text, title);
export const showInfo = (text: string, title: string = "") => Alert.info(text, title);


/**************** Inner (private) class *******************/
class Alert {

  static error(text: string, title: string) {
    return Alert.smallBox({
      title: title,
      content: text,
      color: "#fb3c4a",
      icon: "fa fa-times",
      timeout: ERROR_TIMEOUT,
    }, ERROR_TIMEOUT);
  }

  static warn(text: string, title: string = "Warning") {
    return Alert.smallBox({
      title: title,
      content: text,
      color: "#FF9C00",// "#ffa500",
      icon: "fa fa-exclamation-triangle",
      timeout: WARN_TIMEOUT,
    }, WARN_TIMEOUT);
  }

  static info(text: string, title: string = "Info") {
    return Alert.smallBox({
      title: title,
      content: text,
      color: "#3276B1",
      icon: "fa fa-info-circle",
      timeout: INFO_TIMEOUT,
    }, INFO_TIMEOUT);
  }

  // private ---
  private static smallBox(data, timeOut) {
    $.smallBox(data);
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve();
      }, timeOut);
    });
  }

}
