import {throwError as observableThrowError, Observable} from 'rxjs';
import {
  HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpErrorResponse
} from '@angular/common/http';

import {Router} from "@angular/router";
import {LOGIN_FORM_PAGE, LoginService} from "./login.service";
import {Injectable} from "@angular/core";
import {showWarn} from "../_shared/alert";
import {catchError} from "rxjs/internal/operators";
import {AuthTokenService} from "../_core/auth/auth-token.service";


// Process state of expired session.
@Injectable()
export class ReconnectInterceptor implements HttpInterceptor {

  constructor(private router: Router,
              private loginService: LoginService,
              private authTokenService: AuthTokenService) {
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    return next.handle(req)
      .pipe(
        catchError((err: HttpErrorResponse, caught: Observable<HttpEvent<any>>) => {

          // 401 - Unauthorized (session expired)
          if (err.status === 401) {

            // process only 1st error of a "batch" of requests
            let toSkip = false;

            if (!toSkip) {
              this.authTokenService.removeToken();

              toSkip = true;
              // try to login with stored login & password
              this.loginService.login().subscribe(
                // 1. logged
                () => {
                  // With expired session the last user's action is missed. Notify about it and reload the page!
                  showWarn('New server session established! Repeat your recent action, please.').then(
                    () => {
                      location.reload();
                    }
                  );
                },
                // 2. There is no login pair. Go to login page.
                () => {
                  showWarn('Session is expired. Sing in, please...').then(
                    () => {
                      this.router.navigate([LOGIN_FORM_PAGE]);
                    }
                  );
                },
                // 3. reset toSkip state
                () => {
                  toSkip = false;
                }
              );

              this.router.navigate(['login']);

            }

            return observableThrowError(err);

          }

          return observableThrowError(err);

        })
      );

  }
}
