import {Component, OnInit} from '@angular/core';
import {BalanceService} from "../_core/_app-services/balance.service";

@Component({
  selector: '[ribbon-content]',
  styles: ['h5 {margin: 0; padding: 7px;}'],
  template: `
    <h5 class="text-right">
      balance: {{balance}}&nbsp;
      XTRD
<!--      <select>
        <option>USD</option>
        <option>XTRD</option>
      </select>-->
    </h5>`,
})
export class RibbonContentComponent implements OnInit {
  balance: number;
  constructor(private balanceServ: BalanceService) {}

  ngOnInit(): void {
    this.balanceServ.get()
      .subscribe((res) => this.balance = res.balance)
  }

}
