import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {FormsModule} from "@angular/forms";
import {HttpClientModule} from "@angular/common/http";
import {routing} from "./app.routing";
import {AppComponent} from './app.component';
import {MySmartAdminModule} from "../my-smartadmin/my-smartadmin.module";
import {SmartResolver} from "./_core/rest-api/smart-resolver/smart-resolver";
import {NewsFeedComponent} from "./news-feed/news-feed";
import {CoLocationComponent} from "./co-location/co-location.component";
import {UserComponent} from "./user/user.component";
import {LoginModule} from "./login/login.module";
import {SharedModule} from "./_shared/shared.module";
import {CoreModule} from "./_core/core.module";
import {DashboardComponent} from "./dashboard/dashboard.component";
import {SmartadminHandlerModule} from "./_sa-handler/smartadmin-handler.module";
import {DashboardModule} from "./Dashboard/dashboard.module";
import {ModalModule} from "ngx-bootstrap";
import {OrdersRoutingModule} from "./orders-routing/orders-routing.module";
import {MarketDataModule} from "./market-data/market-data.module";
import {FormValidationHelperService} from "./_core/form-validation-helper/form-validation-helper.service";


@NgModule({
  declarations: [
    AppComponent,

    NewsFeedComponent,
    CoLocationComponent,
    UserComponent,
  ],
  imports: [
    // 1. Angular modules
    BrowserModule,
    FormsModule,
    HttpClientModule,

    // 2. application modules
    // 2.1 core
    CoreModule,

    // 2.2 shared
    SharedModule,

    // 2.3 MySmartAdmin
    MySmartAdminModule,

    // 2.4 control over Smartadmin
    SmartadminHandlerModule,

    // 2.5 login pages module
    LoginModule.forRoot(),

    // 2.6 routing
    routing,

    // 2.7 pages
    DashboardModule,
    OrdersRoutingModule,
    MarketDataModule,

    // 3. ngx-bootstrap / 3-rd party modules
    ModalModule.forRoot()
  ],
  exports: [
  ],
  providers: [
    SmartResolver,
    FormValidationHelperService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
