import {Injectable} from "@angular/core";

const SENT_COLOR = "#90ed7d";
const REST_COLOR = "#434348";

@Injectable()
export class OrdersProgressService {

  composeChartOptions(statData) {

    const chartOptions = {
      chart: {
        type: 'bar',
        height: 200,
      },
      title: false,
      xAxis: {
        categories: ['Order'],
        visible: false
      },
      yAxis: {
        min: 0,
        title: {
          text: 'Orders sending progress'
        }
      },
      tooltip: {
          formatter: function() { return this.y }
        },
      legend: {
        reversed: true,
        verticalAlign: 'top'
      },
      plotOptions: {
        series: {
          stacking: 'normal'
        }
      },
      series: [{
        name: 'Rest',
        data: [statData.amount - statData.sent_orders],
        color: REST_COLOR
      }, {
        name: 'Sent',
        data: [statData.sent_orders],
        color: SENT_COLOR
      }]
    };

    return chartOptions;
  }

}