import {Component, OnInit} from '@angular/core';
import {Transaction} from "../_core/models/transaction";
import {DataSupplierService} from "../_core/rest-api/smart-resolver/data-supplier.service";
import {SortHelper} from "../_shared/sortable-table/sort-helper";

@Component({
  selector: 'xtd-transactions',
  templateUrl: 'transactions.component.html'
})

export class TransactionsComponent implements OnInit {

  private transactions: Transaction[] = null;
  private sortHelper;

  constructor(private ds: DataSupplierService) { }

  ngOnInit() {
    const { transactions } = this.ds.getData();
    this.transactions = transactions.map((t) => new Transaction(t));
    this.sortHelper = new SortHelper(this.transactions, 'id');
  }

  onHeaderClick(e) {
   const clmName = e.target.getAttribute('sortable-column');
    this.sortHelper.onSortByColumn(clmName);
  }

  getTransactions() {
    return this.sortHelper.sort();
  }
}