import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from "@angular/common/http";
import {Observable} from "rxjs";
import {tap} from "rxjs/operators";
import {REST_BASE, TO_USE_REST_MOCKUP} from "../../../app.config";
import {mockResponseMap} from "./mock-response-map";
import {delay} from "rxjs/internal/operators";

export class MockInterceptor implements HttpInterceptor {

  originalPath: string[] = [];

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    // 1. check if Mock is turn on otherwise exception.
    if (!TO_USE_REST_MOCKUP) {
      throw new Error('comment out MockInterceptor in rest-api.module.ts because mock was disabled!');
      // return next.handle(req);
    }

    // Note: potential problem with async requests!
    const oPath = req.url.replace(REST_BASE, '');

    // 2. If there is no URL mock map then make real request!
    if (!mockResponseMap[oPath]) {
      return next.handle(req);
    }

    this.originalPath.push(oPath);

    // 3. substitute request.
    const mockPathReq = req.clone({url: '/', method: 'get', responseType: 'text'});
    const onReqFinish = (event: HttpEvent<any>) => {
      if (event.type === undefined || event.type === 4) {
        event['body'] = mockResponseMap[this.originalPath.shift()];
      }
    };

    return next.handle(mockPathReq)
      .pipe(
        tap(onReqFinish),
      );
  }
}
