import { Component } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {CO_LOCATION_ROUTE, MARKET_DATA_ROUTE, NEWS_FEED_ROUTE, ORDERS_ROUTING_ROUTE} from "../../app.routing";

// Note: maybe, in the future, it should be app service accessible for other components.
const SERVICE_TO_ROUTE_MAP: {[id: number]: string} = {
  1: MARKET_DATA_ROUTE,
  2: ORDERS_ROUTING_ROUTE,
  3: CO_LOCATION_ROUTE,
  4: NEWS_FEED_ROUTE
};

@Component({
 selector: 'xtd-serevices',
 templateUrl: './xtrd-services.component.html'
})

export class XtrdServicesComponent {
  // TODO: add models & types
  xtrdServices = null;
  constructor(route: ActivatedRoute) {
    this.xtrdServices = route.snapshot.data['resolvedData']['xtrdServices'];
  }

  getServiceRoute(id: number): string {
    return '/' + SERVICE_TO_ROUTE_MAP[id];
  }
}