export function enumerable(value: boolean) {
  return function (target: any, propertyKey: string, descriptor: PropertyDescriptor) {
    descriptor.enumerable = value;
  };
}

export function notenumerable(target: any, propertyKey: string, descriptor: PropertyDescriptor) {
  descriptor.enumerable = false;
}


// used by collection
export function extendArray(constructor: Function) {
  Object.getOwnPropertyNames(constructor.prototype)
    .filter(name => name !== 'constructor')
    .forEach(name => {
      const attributes = Object.getOwnPropertyDescriptor(constructor.prototype, name);
      Object.defineProperty(Array.prototype, name, attributes);
    });
}
