import {Injectable} from "@angular/core";

const FILLED_COLOR = "#90ed7d";
const REJECTED_COLOR = "#434348";

@Injectable()
export class ExecutionBreakdownsService {

  composeChartOptions(statData) {
    const filledSum = statData.reduce((sum, obj) => sum + obj.filled, 0);
    const rejectedSum = statData.reduce((sum, obj) => sum + obj.rejected, 0);

    const chartOptions =
      {
        chart: {
          type: "pie"
        },
        title: false,
        plotOptions: {
          pie: {
            shadow: false,
            center: [
              "50%",
              "50%"
            ],
            legend: { // not work (?!)
              layout: 'vertical',
              align: 'right',
              verticalAlign: 'top',
              floating: true,
            },
          }
        },
        tooltip: {
          formatter: function() { return this.key + ': ' + this.y }
        },
        legend: {
          verticalAlign: 'top'
        },
        series: [
          // 1. Execution Results - inner circle
          {
            name: "Execution Results",
            size: "60%",
            dataLabels: {
              format: '{point.y}',
              color: "#ffffff",
              distance: -50
            },
            showInLegend: true,
            data: [
              {
                name: "Filled",
                y: filledSum,
                color: FILLED_COLOR
              },
              {
                name: "Rejected/Canceled",
                y: rejectedSum,
                color: REJECTED_COLOR
              }
            ]
          },
          // 2. Exchanges - outer ring
          {
            name: "Exchanges",
            size: "100%",
            innerSize: "60%",
            dataLabels: false
          }
        ]
      };

    // fill up data of exchanges
    // 1. filled (success)
    const fArr = statData.map((e: any) => {
      return {name: e.name, y: e.filled, color: FILLED_COLOR}
    });
    // 2. rejected
    const rArr = statData.map((e: any) => {
      return {name: e.name, y: e.rejected, color: REJECTED_COLOR}
    });
    chartOptions.series[1].data = fArr.concat(rArr);

    return chartOptions;

  }


}
