import {Component, OnInit} from '@angular/core';
import {MarketDataService} from "./market-data.service";
import {BsModalRef, BsModalService, ModalOptions} from "ngx-bootstrap";
import {AddMarketDataConnectorComponent} from "./add-market-data-connector/add-market-data-connector.component";
import {DataSupplierService} from "../_core/rest-api/smart-resolver/data-supplier.service";
import {ExchangeMdSelectable} from "./exchange-md-selectable";


@Component({
  selector: 'xtd-market-data',
  templateUrl: './market-data.component.html'
})

export class MarketDataComponent implements OnInit {
  private exchanges: ExchangeMdSelectable[];
  appliedExchanges: ExchangeMdSelectable[];
  private bsModalRef: BsModalRef;

  constructor(private ds: DataSupplierService, private modalService: BsModalService) { }

  ngOnInit() {
    let {exchanges, appliedExchangesIds} = this.ds.getData();
    this.exchanges = exchanges.map((e) => new ExchangeMdSelectable(e));
    this.appliedExchanges = this.exchanges.filter(e => appliedExchangesIds.indexOf(e.id) !== -1);
  }

  showAddExchangeModal() {
    const appliedExchangesIds = this.appliedExchanges.map((e) => e.id);
    const initialState = {
      notAppliedExchanges: this.exchanges.filter(e => appliedExchangesIds.indexOf(e.id) === -1),
      modalSubmitCallback: this.onConnectorsAppending.bind(this)
    };
    this.bsModalRef = this.modalService.show(AddMarketDataConnectorComponent, <ModalOptions>{initialState});
  }

  onConnectorsAppending(newAppliedExchanges: ExchangeMdSelectable[]) {
    newAppliedExchanges.forEach((e) => this.appliedExchanges.push(e));
  }

}
