import {Component, OnInit} from '@angular/core';
import {DataSupplierService} from "../_core/rest-api/smart-resolver/data-supplier.service";
import {Collection} from "../_core/models/_base/collection";
import {Exchange} from "../_core/models/exchange";
import {BsModalRef, BsModalService, ModalOptions} from "ngx-bootstrap";
import {AddNewRoutingComponent} from "./add-new-routing.component";
import {OrdersRouting} from "../_core/models/ordersRouting";
import {OrdersTimelineService} from "./orders-timeline.service";
import {SortHelper} from "../_shared/sortable-table/sort-helper";

class OrdersRoutingUI extends OrdersRouting {
  exchange_name?: string;
}

@Component({
  selector: 'xtd-orders-routing',
  templateUrl: 'orders-routing.component.html',
  providers: [OrdersTimelineService]
})

export class OrdersRoutingComponent implements OnInit {

  ordersRoutings: OrdersRoutingUI[];
  exchanges: Collection<Exchange>;
  modalRef: BsModalRef;

  ordersTimelineOptions: any;
  sortHelper: SortHelper;

  constructor(private modalService: BsModalService,
              private ordTimelineServ: OrdersTimelineService,
              private ds: DataSupplierService) { }

  ngOnInit() {
    const { exchanges, ordersRoutings, ordersTimeline } = this.ds.getData();
    this.ordersRoutings = ordersRoutings.map((o) => {
      o.exchange_name = exchanges.getItemByParam({id: o.exchange_id}).name;
      return new OrdersRoutingUI(o);
    });

    this.exchanges = new Collection(exchanges);

    this.ordersTimelineOptions = this.ordTimelineServ.composeOrdersTimelineChart(ordersTimeline);
    this.sortHelper = new SortHelper(this.ordersRoutings, 'connection_name');
  }

  getOrdersRoutings() {
    return this.sortHelper.sort();
  }

  showAddRouterDialog() {
    const initialState = {
      exchanges: this.exchanges,
      addRouterDialogCallback: this.addRouterDialogCallback.bind(this)
    };

    this.modalRef = this.modalService.show(AddNewRoutingComponent, <ModalOptions>{initialState});
  }

  addRouterDialogCallback(or: OrdersRouting) {
    this.ordersRoutings.push(<OrdersRoutingUI>or);
  }

  onHeaderClick(e) {
    const clmName = e.target.getAttribute('sortable-column');
    this.sortHelper.onSortByColumn(clmName);
  }
}
