import {Injectable} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {SAVE_MARKET_DATA_OPTIONS} from "../../_core/rest-api/rest-api";
import {Observable} from "rxjs";

@Injectable()
export class AddMarketDataConnectorService {
  constructor(private http: HttpClient) {
  }

  // accepts array of IDs
  save(data: number[]): Observable<any> {
    return this.http.put(SAVE_MARKET_DATA_OPTIONS, data)
  }
}