import {Directive, ElementRef, HostListener, Input, OnInit, Renderer2} from '@angular/core';

/*
* Usage:
*
* sortable-column="<column/field name>"
* sortable-column-init - parameter to make a coulumn as active initially
*
* */

const TH_CLASS_NAME = 'sort';
const TH_ACTIVE_CLASS_NAME = 'active';
const TH_DESC_CLASS_NAME = 'desc';

@Directive({selector: '[sortable-column]'})
export class SortableColumnDirective implements OnInit {
  private domEl;

  @HostListener('click')
  onColumnClick() {

    const elCL = this.domEl.classList;
    const isCurrent = elCL.contains(TH_ACTIVE_CLASS_NAME);
    if (isCurrent) {
      elCL.toggle(TH_DESC_CLASS_NAME);
    }
    else {
      const tr = this.domEl.parentNode;
      const actTh = tr.querySelectorAll('th.' + TH_ACTIVE_CLASS_NAME)[0];
      if (actTh) {
        actTh.classList.remove(TH_ACTIVE_CLASS_NAME);
      }
      elCL.add(TH_ACTIVE_CLASS_NAME);
      elCL.remove('desc');
    }
  }


  constructor(private el: ElementRef, private renderer: Renderer2) {}

  ngOnInit() {
    this.domEl = this.el.nativeElement;
    this.renderer.addClass(this.domEl, TH_CLASS_NAME);

    if (this.domEl.getAttribute('sortable-column-init') !== null) {
      this.renderer.addClass(this.domEl, TH_ACTIVE_CLASS_NAME);
    }

    const arrow1 = this.renderer.createElement('i');
    this.renderer.addClass(arrow1, 'fa');
    this.renderer.addClass(arrow1, 'fa-caret-up');

    const arrow2 = this.renderer.createElement('i');
    this.renderer.addClass(arrow2, 'fa');
    this.renderer.addClass(arrow2, 'fa-caret-down');

    this.renderer.addClass(this.domEl, TH_CLASS_NAME);
    this.renderer.appendChild(this.domEl, arrow1);
    this.renderer.appendChild(this.domEl, arrow2);
  }

}