import {Injectable} from "@angular/core";

@Injectable()
export class OrdersTimelineService {

  composeOrdersTimelineChart(statData): any {
    const chartOptions: any =
      {
        chart: {
          type: "column",
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false
        },
        title: {
          text: "Orders per hour"
        },
        xAxis: {
          type: 'category'
        },
        yAxis: {
          title: {enabled: false}

        },
        tooltip: {
          formatter: function () {
            return this.y + ' orders';
          }
        },
        series: [
          {name: "Orders per hour"}
        ],
        legend: {
          enabled: false
        }
      };

    chartOptions.series[0].data = statData.map((d) => {
      return {name: d.time, y: d.amount}
    });

    return chartOptions;
  }
}