import {Component} from '@angular/core';
import {BsModalRef} from "ngx-bootstrap";
import {Exchange} from "../../_core/models/exchange";
import {showError, showInfo} from "../../_shared/alert";
import {AddMarketDataConnectorService} from "./add-market-data-connector.service";
import {ExchangeMdSelectable} from "../exchange-md-selectable";

@Component({
  templateUrl: 'add-market-data-connector.component.html',
  styleUrls: ['add-market-data-connector.component.css'],
  providers: [AddMarketDataConnectorService]
})

export class AddMarketDataConnectorComponent {
  // modal inputs parameters
  notAppliedExchanges: ExchangeMdSelectable[];
  modalSubmitCallback;

  constructor(public bsModalRef: BsModalRef, private addCnrServ: AddMarketDataConnectorService) { }

  submit() {

    const selectedExcs: Exchange[] = this.notAppliedExchanges.filter(e => e.isSelected);
    const selectedIds: number[] = selectedExcs.map(e => e.id );

    this.addCnrServ.save(selectedIds).subscribe(
      () => {
        this.bsModalRef.hide();
        this.modalSubmitCallback(selectedExcs);
        showInfo('Market data configuration was save successfully.');
      },
      (err) => showError('Failed to select notAppliedExchanges.')
    )
  }

}