import {Injectable} from "@angular/core";

@Injectable()
export class ExchangesOrdersService {

  composeChartOptions(statData) {
    let chartOptions: any =
      {
        chart: {
          type: 'pie',
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false
        },
        title: false,
        tooltip: {
          formatter: function() { return this.key + ': ' + this.y }
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: 'pointer',
            showInLegend: true,
          }
        },
        legend: {
          verticalAlign: 'top'
        },
        showInLegend: true,
        series: [{
          name: 'Exchanges',
          colorByPoint: true,
          dataLabels: false
        }]
      };

    chartOptions.series[0].data = statData.map((d) => {
      return {name: d.name, y: d.amount}
    });

    return chartOptions;
  }


}