import {Component, Input, OnInit} from '@angular/core';
import {SortHelper} from "../../_shared/sortable-table/sort-helper";
import {ExchangeMdSelectable} from "../exchange-md-selectable";

@Component({
  selector: 'xtd-connectors-table',
  templateUrl: 'connectors-table.component.html',
  styleUrls: ['connectors-table.component.css']
})

export class ConnectorsTableComponent implements OnInit {

  @Input() exchanges: ExchangeMdSelectable[];
  @Input() isSelector: boolean;
  private sortHelper;

  ngOnInit() {
    // reset if previous selection which was not submitted
    this.exchanges.forEach((e) => e.isSelected = false);
    this.sortHelper = new SortHelper(this.exchanges, 'name');
  }

  onHeaderClick(e) {
    const clmName = e.target.getAttribute('sortable-column');
    this.sortHelper.onSortByColumn(clmName);
  }

  selectAll(isChecked) {
    this.exchanges.forEach((e) => e.isSelected = isChecked);
  }

  getConnectors() {
    return this.sortHelper.sort();
  }

}