import {enumerable, extendArray} from './_decorators';

@extendArray
export class Collection<T> extends Array<T> {
  constructor(...args: T[]) {
    // 1. wraps array passed as a single parameter
    if (args.length === 1 && Array.isArray(args[0])) {
       const arr = args[0] as any;
       super(...arr);
    }
    // 2. new array creation
    else {
      super(...args);
    }
  }

  @enumerable(false)
  getItemByParam(paramPair: {[key: string]: string | number | boolean}): T {
    const key = Object.keys(paramPair)[0];
    return this.find(function(item){return ((item[key] == paramPair[key]) ? true: false)});
  }

  @enumerable(false)
  deleteItem(obj: T, uniqueKeyName?: string): Array<T> {
    let idx;
    if (uniqueKeyName) {
      const value = obj[uniqueKeyName];
      idx = this.indexOf(this.getItemByParam({uniqueKeyName: value}));
    }
    else {
      idx = this.indexOf(obj);
    }
    return this.splice(idx, 1);
  }

  @enumerable(false)
  get lastItem() {
    return this[this.length - 1];
  }
  set lastItem(value: T) {
    this[this.length - 1] = value;
  }

}
