import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from "@angular/common/http";
import {Observable} from "rxjs";
import {REST_BASE} from "../../app.config";

// adds base url to the rest request.
export class RestBaseInterceptor implements HttpInterceptor {
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const fullPathReq = req.clone({ url: (REST_BASE + req.url) });
    return next.handle(fullPathReq);
  }
}
