
import {throwError as observableThrowError, Observable} from 'rxjs';

import {HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from "@angular/common/http";
import {Injectable} from "@angular/core";
import {catchError} from "rxjs/operators";
import {showError, showWarn} from "../../_shared/alert";

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {

  constructor() {
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(req)
      .pipe(
        catchError((err: HttpErrorResponse) => {

            // 1. A client-side or network error occurred. Handle it accordingly.
            if (err.error instanceof Error) {
              showError('Network Error:', err.error.message);
              return observableThrowError(err);
            }


            // 2. The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong

            // 2.1 Unauthorized - Can be caused by incorrect/expired token provided
            // if (err.status === 401) {} - reconnect.interceptor processes 401!

            // 2.2 Forbidden - Only admin user can do this operation
            if (err.status === 403) {
              showWarn('Forbidden', err.error.message);
              return observableThrowError(err);
            }

            // 2.3 not found resource.
            // In our API, 404 means that no data was found on the server!
            // smart resolver handles error itself.
            // else if (err.status === 404) {} - use "empty" data or issue additional request.

            // 2.4 Server Error - show an message on each other error.
            else if (err.status === 500) {
              showError("Failed to get data from the server.");
              return observableThrowError(err);
            }
          }
        )
      );
  }
}
