const TOKEN_KEY = 'auth_token';


export class AuthTokenService {

  private authToken: string = null;

  isAuthorized() {
    return !!this.getToken();
  }

  // token ---
  saveToken(token: string) {

    if (typeof token == 'object') {
      token = token['token'];
    }

    if (!token) {
      this.removeToken();
      return;
    }
    this.authToken = token;
    sessionStorage.setItem(TOKEN_KEY, this.authToken);
  }

  getToken() {
    return this.authToken = ( this.authToken || sessionStorage.getItem(TOKEN_KEY));
  }

  removeToken() {
    this.authToken = null;
    sessionStorage.removeItem(TOKEN_KEY);
  }






}
