import {NgModule} from '@angular/core';
import {RouterModule} from "@angular/router";
import {NavigationContentComponent} from "./navigation-content.component";
import {WidgetStylingDirective} from "./widget-styling.directive";
import {LogoutButtonComponent} from "./logout-button.component";
import {RibbonContentComponent} from "./ribbon-content.component";


// Note: include this shared in any SmartAdmin module to declare its components.

@NgModule({
  imports: [
    RouterModule
  ],
  declarations: [
    NavigationContentComponent,
    RibbonContentComponent,
    WidgetStylingDirective,
    LogoutButtonComponent
  ],
  exports: [
    NavigationContentComponent,
    RibbonContentComponent,
    WidgetStylingDirective,
    LogoutButtonComponent
  ]
})
export class SmartadminHandlerModule {
}
